// ESM syntax is supported.

function saveIssueToLocalStorage(issueId, issueDesc, issueSeverity, issueAssignedTo, issueStatus) {
    var issue = {
        id: issueId,
        description: issueDesc,
        severity: issueSeverity,
        assignedTo: issueAssignedTo,
        status: issueStatus
    };

    var issues = [];
    if (localStorage.getItem('issues') == null) {
        issues.push(issue);
        localStorage.setItem('issues', JSON.stringify(issues));
    } else {
        issues = JSON.parse(localStorage.getItem('issues'));
        issues.push(issue);
        localStorage.setItem('issues', JSON.stringify(issues));
    }
}

function setIssueStatusClosed(id) {
    var issues = JSON.parse(localStorage.getItem('issues'));

    for (var i = 0; i < issues.length; i++) {
        if (issues[i].id == id) {
            issues[i].status = 'Closed';
        }
    }

    localStorage.setItem('issues', JSON.stringify(issues));
}
  
// eslint-disable-next-line no-unused-vars
function deleteIssueFromLocalStorage(id) {
    var issues = JSON.parse(localStorage.getItem('issues'));
  
    for (var i = 0; i < issues.length; i++) {
      if (issues[i].id == id) {
        issues.splice(i, 1);
      }
    }
  
    localStorage.setItem('issues', JSON.stringify(issues));
}

export { saveIssueToLocalStorage, setIssueStatusClosed, deleteIssueFromLocalStorage };
